
import { Provider } from 'react-redux'
import store from './redux/store'
import './App.css';
import CakeContainer from './components/cakeContainer'
import HooksCakeContainer from './components/HooksCakeContainer'
import IceCreamContainer from './components/iceCreamContainer'
import NewCakeContainer from './components/NewCakeContainer'
import ItemContainer from './components/ItemContainer'
import UserContainer from './components/UserContainer'



import React, { Component } from 'react';
import TopMenu from './components/TopMenu'
const editors = {};
class App extends Component {
  constructor(props) {
    super(props);
  this.state = { TampleteNameList: []}
  }
  make_one_editor(editor_name) {
    editors[editor_name] = window.CKEDITOR.replace(editor_name, {
      extraPlugins: ['print', 'sharedspace', 'image2', 'uploadwidget',
        'uploadimage', 'justify', 'tableresize', 'font',
        'colorbutton', 'tabletools', 'contextmenu', 'colordialog',
        'pastefromexcel', 'clipboard', 'sourcedialog', 'justify', 'clipboard', 'pagebreak'],
      sharedSpaces: {
        top: 'top2'
      }
    });

  }
  initCkEditor() {
    window.CKEDITOR.disableAutoInline = true;

    window.CKEDITOR.config.removePlugins = 'elementspath';


    let editorIdList = ['editor1']
    for (let i = 0; i < editorIdList.length; i++) {
      this.make_one_editor(editorIdList[i])
    }

    window.CKEDITOR.config.height = '16cm'
    window.CKEDITOR.config.language = 'he';
  }

  componentDidMount() {
    this.initCkEditor()

  }
  render() {
    return (
      <Provider store={store}>
        <div className="App">

          <div className="container">
            <TopMenu />
            <div className="row">
              <div className="col-sm-2">
                <div className="row top">
                  הוסף חלק מטופס
            </div>


                <div className="row contacts"
                  style={{
                    display: 'block',
                    margin: 0
                  }} >
                  <div className="contacts_padding" >

                    <select name="cars" size="7" style={{ width: '90%' }}>
                      <option >gdf hgfh</option>
                      <option >2222</option>
                      <option >7777</option>
                      {/*<option value="audi">Audi</option>*/}
                    </select>

                    <button type="button" className="btn btn-primary" className="btnAddNewPart" onClick={this.AddPartForm} >
                      הוסף חלק טמפלט קיים
                </button>
                    <hr />

                  </div>
                </div>
              </div>
              <div className="col-sm-8">
                <textarea name="content" id="editor1">
                </textarea>
              </div>
              <div className="col-sm-2">
                <div className="row top">
                  <label className="ChooseTamplateHeadline">בחר טמפלט מתבנית קיימת:</label>
                </div>
                <div className="row no_margin_select_row">

                  <div className="col-sm-12">

                    <select name="files" size="7" style={{ width: '100%' }} onChange={this._handleTemplateChange}>
                      {this.state.TampleteNameList}
                    </select>
                  </div>
                  <button onClick={() => this.makeTables()} className="showTamplateBtn"> צור טופס</button>

                </div>
              </div>
            </div>
          </div>
          {/*<UserContainer/>
      <ItemContainer cake/>
      <ItemContainer/>
      <CakeContainer/>
      <HooksCakeContainer/>
      <IceCreamContainer/>
    <NewCakeContainer/>*/}
        </div>
      </Provider>
    );
  }
}

export default App;
